
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IVoucherThemeForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import VoucherThemeForm from '@/views/sale/voucher-theme/forms/VoucherThemeForm.vue'

export interface IData {
  model: IVoucherThemeForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IVoucherThemeForm) => {
      return store.dispatch('voucherTheme/createForm', dataForm)
    }) as ResponseFetching<Promise<IVoucherThemeForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    VoucherThemeForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        name: 'New voucher-theme',
        image: ''
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'voucher-theme' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: IVoucherThemeForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'voucher-theme-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
