
import { defineComponent, PropType, reactive, ref } from 'vue'
import type { FormRules } from 'element-plus'
import { IVoucherThemeForm } from '@/types'
import FormMixin from '@/mixins/form.mixin'
import { FormRule } from '@/constants/validators'

export interface IData {
  model: IVoucherThemeForm;
  isValid: boolean;
}

export default defineComponent({
  name: 'VoucherThemeForm',

  mixins: [FormMixin],

  setup () {
    return {
      form: ref(),
      rules: reactive<FormRules>(
        FormRule
          .templates()
          .name()
          .image()
          .close()
      )
    }
  },

  props: {
    modelValue: Object as PropType<IVoucherThemeForm>,

    isLoading: Boolean as PropType<boolean>,

    isNew: {
      type: Boolean,
      default: false
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || {
        name: '',
        image: ''
      },

      isValid: false
    }
  }
})
